.TodoSeach{
    background: #F9FBFC;
    border-radius: 2px;
    border: 2px solid #61dafa;
    margin: 0 24px;
    height: 60px;
    width: calc(100%-62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #61dafa;
    box-shadow: 0px 5px 25px rgba(97,218,250,0.5);
    margin-left: 30%;
    width: 40%;
    border-radius: 20px;
    
}

.TodoSeach::placeholder{
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

.TodoSeach:focus{
    outline-color: #61DAFA;
}